

.destino__item{
    max-width: 100vw;
    margin: 0 1rem;
}
.destino__container{
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 100vw;
}
.destino__box{
    margin: 0.5rem 1rem;
    position:relative;
    max-width: 100%;
}
 .destino__box img {
     position:relative;
 width:100%
     } 
.destino__box p{
    color: ivory;
    font-size: 2rem;
    margin: 0.5rem;
    position:absolute ;
    top:0;
    left:0;
    text-shadow: 2px 2px 2px black;
     }
.catalogo_container{
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.catalogo__box{
    background-size: cover;
    height: 15rem;
    margin: 1rem 1rem;
    max-width: 100vw;

}
.catalogo__box__link{
    color:ivory;
    font-size: 2rem;
    text-shadow:2px 2px 2px black;
}
.contacto__destino{
    align-items: center;
    background-image: url(../../images/contacto.jpeg);
    background-size: cover;
    color:ivory;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 15rem;
    margin: 3rem 0 1rem; 
    max-width: 100vw;
    text-shadow:1px 1px 1px black;
}
.contacto__destino__link{
    background-color:darken($color: $gold, $amount: 20) ;
    color: black;
    font-size: 1.2rem;
    font-weight: bolder;
    padding: 0.5rem 1.5rem;
    text-shadow: none;
}
.oferta__destino{
    align-items: center;
    background-image: url(../../images/oferta.jpeg);
    background-size: cover;
    color:black;
    display: flex;
    flex-direction: column;
    font-size: 1.3rem;
    justify-content: space-around;
    height: 15rem;
    margin: 3rem 0 1rem; 
    max-width: 100vw;
    text-shadow:1px 1px 1px ivory;
}